<template>
  <div v-for="job in jobs" :key="job.id">
    <div class="job_company" @click="job.show = !job.show">
      <span>{{ job.company }} | {{ job.location }} </span> - {{ job.job_title }}
      <span v-if="!job.show">[V]</span>
      <span v-if="job.show">[^]</span>

    </div>
    <div class="job_description" v-show='job.show'>
      {{ job.date_start }} - {{ job.date_end }}
    </div>
    <div class="job_description" v-show='job.show' v-for="description in job.descriptions" :key="description.id">
      - {{ description }}
    </div>
    <br>
  </div>
</template>

<script>
import sourceData from '@/jobData.json'
export default {
  name: 'job_description',
  props: {
    msg: String
  },
  data() {
    return {
      jobs: sourceData.jobs
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
span {
  color: deeppink;
  font-weight: bold;
}

.job_description {
  font-family: monospace;
}
</style>