<template>
  <div v-for="project in projects" :key="project.id">
    <div class="project_company" @click="project.show = !project.show">
      <span>{{ project.title }} </span> - {{ project.technologies }}
      <span v-if="!project.show">[V]</span>
      <span v-if="project.show">[^]</span>
    </div>
    <div v-show='project.show'>
      <div class="project_description">
        {{ project.date }}
      </div>
      <div class="project_description" v-for="description in project.descriptions" :key="description.id">
        - {{ description }}
      </div>
      <span v-show="project.github" class="link">
        <a v-bind:href="project.github" target="_blank">github</a>
      </span>
      &nbsp;
      <span v-show="project.video" class="link">
        <a class="video_link" @click="project.showVideo = !project.showVideo"
          :style="project.showVideo ? { 'color': 'purple' } : { 'color': 'deeppink' }">video</a>
      </span>
      <br>
      <iframe v-show="project.showVideo" width="640" height="360" v-bind:src="project.video">
      </iframe>
    </div>
    <br>
  </div>
</template>

<script>
import sourceData from '@/projectData.json'
export default {
  name: 'project_description',
  props: {
    msg: String
  },
  data() {
    return {
      projects: sourceData.projects
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
span {
  color: deeppink;
  font-weight: bold;
}

.project_description {
  font-family: monospace;
}

a {
  color: deeppink;
}

a:visited {
  color: purple;
}

.link {
  color: deeppink;
  text-decoration: underline;
}
</style>